import { Link } from 'react-router-dom'
import { Component } from 'react'
import ContactList from '../../components/ContactList/ContactList.jsx'
import ContactFilter from '../../components/ContactFilter/ContactFilter.jsx'
import contactService from '../../services/contactService.js'


import './ContactPage.scss'

export class ContactPage extends Component {

  state = {
    contacts: null,
    filterBy: {
      term: null
    },
  }

  componentDidMount() {
    this.loadContacts()
  }

  async loadContacts() {
    const contacts = await contactService.getContacts(this.state.filterBy)
    this.setState({ contacts })

  }



  onDeleteContact = async (contactId) => {
    await contactService.deleteContact(contactId)
    this.loadContacts()
  }


  onChangeFilter = (filterBy) => {

    this.setState({ filterBy }, this.loadContacts)
  }

  render() {
    const { contacts } = this.state
    return (
      contacts && <div className="contact-container">
        { <ContactFilter match={this.props.match} onChangeFilter={this.onChangeFilter} />}
        <Link to="/contact/edit">➕</Link>
        {  <ContactList contacts={contacts} onDeleteContact={this.onDeleteContact} />}
      </div>
    )
  }
}
