
import { Component } from 'react'
import { Link } from 'react-router-dom';
import contactService from '../../services/contactService.js'
import './ContactDetails.scss'

export class ContactDetails extends Component {

    state = {
        contact: null
    }

    componentDidMount() {
        this.loadContact()
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevProps.match.params.id !== this.props.match.params.id) {
            this.loadContact()
        }
    }

    async loadContact() {
        const contact = await contactService.getContactById(this.props.match.params.id)
        this.setState({ contact })
        console.log(this.props)

    }

    render() {
        const { contact } = this.state
        if (!contact) return <div>Loading contact.....</div>
        return (
            <div className="contact-details">
                <div className="actions">
                    <Link className="link-btn" to={'/contact/'}>⏮</Link>
                    <button onClick={() => this.props.onDeleteContact(contact._id)}>Delete</button>
                    <Link className="link-btn"  to={'/contact/edit/' + contact._id}>📝</Link>
                </div>
                <div className="contact-info"> 
                <img className="contact-details-img" src={`https://robohash.org/${contact._id}`} alt="" />
                <span> Name: {contact.name}</span>
                <span> Email: {contact.email}</span>
                <span>Phone: {contact.phone}</span>
                </div>


            </div>
        )
    }
}
